import React, { useCallback, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Canvas from '../../components/gameplay/Canvas';
import { clickPieceThunk, initializeBlocked } from '../../modules/canvas';
import { clearValue } from '../../modules/canvas';

const CanvasContainer = ({ cellSize }) => {
    const { board, reverseBoard, reversed, blocked, turn, role } = useSelector(({ canvas, socketAuth, game, record }) => ({
        board: canvas.board,
        reverseBoard: canvas.reverseBoard,
        reversed: record.reversed,
        blocked: canvas.blocked,
        turn: game.turn,
        role: socketAuth.role,
    }));

    const dispatch = useDispatch();
    
    useEffect(() => {
        if((role === 'white' && turn % 2 === 0) || (role === 'black' && turn % 2 === 1)) {
            dispatch(initializeBlocked({ blocked: false }));
        } else {
            dispatch(initializeBlocked({ blocked: true }));
        }
    }, [dispatch, turn, role]);
    
    useEffect(() => {
        return () => {
            dispatch(clearValue());
        }
    }, [dispatch])

    //  function   Context    redux state   
    //   event handler React dom   dom rerender  
    //   state     .  ref    
    //   redux state    thunk   

    const onClick = useCallback((y, x) => {
        dispatch(clickPieceThunk({ y, x }));
    }, [dispatch]);

    let reversal = false;
    if(((role === 'white' || role === 'spectator') && reversed) || (role === 'black' && !reversed)) {
        reversal = true;
    }

    let servedBoard = board;
    if(reversal) {
        servedBoard = reverseBoard;
    };

    return (
        <Canvas
            board={servedBoard}
            blocked={blocked}
            onClick={onClick}
            cellSize={cellSize}
        />
    )
};

export default CanvasContainer;